/*
**  Main structure
*/

/* Main body ID */
div#root {
  min-width:        100vw;
  min-height:       100vh;

  margin:           0;
  padding:          0;

  font-size:        12px;

  display:          -ms-flexbox;

  display:          flex;
  -ms-flex-direction:   column;
      flex-direction:   column;
  -ms-flex-align:      center;
      align-items:      center;
  -ms-flex-pack:  center;
      justify-content:  center;
}

@media(min-width: 2000px) { body#falcon-body { font-size: 24px; } }

/* Common structural properties */
header#falcon-header,
main#falcon-container,
footer#falcon-footer { min-width: 100%; }

/* Main header ID */
header#falcon-header {
  padding:    1em;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

/* Main container ID */
main#falcon-container {
  -ms-flex: 1 1;
      flex: 1 1;
}

/*
**  Flexbox classes
*/

.flex-row,
.flex-col { display: -ms-flexbox; display: flex; }

.flex-row { -ms-flex-direction: row; flex-direction: row; }
.flex-col { -ms-flex-direction: column; flex-direction: column; }

/* Vertical and horizontal centering */
.flex-row.center-v,
.flex-col.center-h { -ms-flex-align: center; align-items: center; }

.flex-col.center-v,
.flex-row.center-h { -ms-flex-pack: center; justify-content: center; }

.flex-middle {
  -ms-flex-align:      center;
      align-items:      center;
  -ms-flex-pack:  center;
      justify-content:  center;
}

/* Top/bottom, left/right alignments */

.flex-row.flex-top,
.flex-col.flex-left { -ms-flex-align: start; align-items: flex-start; }

.flex-row.flex-bottom,
.flex-col.flex-right { -ms-flex-align: end; align-items: flex-end; }

.flex-row.flex-left,
.flex-col.flex-top { -ms-flex-pack: start; justify-content: flex-start; }

.flex-row.flex-right,
.flex-col.flex-bottom { -ms-flex-pack: end; justify-content: flex-end; }

/* HR fix */
.flex-row > hr,
.flex-col > hr { width: 100%; }

/*
**  Title styling classes
*/

h1.centered-title,
h2.centered-title,
h3.centered-title,
h4.centered-title,
h5.centered-title,
h6.centered-title {
  min-width:  100%;

  text-align: center;
}



